.container {
  display: flex;
  width: 100%;
  background: inherit;
}

.buttonWrapper {
  display: flex;
  align-items: flex-end;
  z-index: 2;
  background: inherit;
}

.button {
  border: none;
  text-decoration: none;
  cursor: pointer;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  color: white;
  font-size: 20px;
  font-weight: bold;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin-bottom: 5px;
}

.dateListScrollable {
  display: flex;
  overflow-x: scroll;
  scrollbar-width: none;
  margin: 2px 0 2px -40px;
  -webkit-overflow-scrolling: touch;
}

.dateListScrollable::-webkit-scrollbar {
  -webkit-appearance: none;
  display: none;
}

.monthContainer {
  display: flex;
  flex-direction: column;
}

.monthYearLabel {
  align-self: flex-start;
  z-index: 3;
  font-size: 15px;
  font-weight: bold;
  position: sticky;
  top: 0;
  left: 0;
  width: max-content;
  margin: 0 14px 10px 0;
}

.dateDayItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 2px;
  width: 45px;
  height: 49px;
  flex-shrink: 0;
  justify-content: center;
}

@media screen and (min-width: 400px) {
  .dateDayItem {
    cursor: pointer;
  }
}

.daysContainer {
  display: flex;
  z-index: 1;
}

.dayLabel {
  font-size: 10px;
}

.dateLabel {
  font-size: 15px;
}
